#svg2 {
  max-height: 644px;
}

.wrapper path {
  transition: all 0.5s;
  stroke: rgba(0, 0, 0, 0.5);
}

/* Калассы для субъектов при наведение */
.wrapper .active {
  transition: all 0.5s;
  opacity: 1;
}

.wrapper .unactive {
  opacity: 0.5;
}

/* Федеральные округа */
.CFD {
  fill: lightcyan;
}
.UFD {
  fill: lightgreen;
}
.SFD {
  fill: lightyellow;
}
.VFD {
  fill: lightslategray;
}
.FEFD {
  fill: lightcoral;
}
.NWFD {
  fill: lightsalmon;
}
.NCFD {
  fill: lightpink;
}
.SoFD {
  fill: lightskyblue;
}

.indicator {
  position: absolute;
  display: inline;
  background: rgba(0, 0, 0, 1);
  color: #f1f1f1;
  padding: 10px 30px;
  font: 20px Arial;
  border-radius: 5px;
  min-width: 300px;

  text-transform: capitalize;
  display: none;
}

.subject-content #map {
  max-width: 685px;
  height: 643px;
}
