.block-title {
  display: flex;
  align-items: center;
  gap: 17px;

  margin-bottom: 23px;
  margin-top: 35px;
}

.title.atlas__subject-title {
  color: #224390;
  text-transform: none;
  margin: 0;

  min-width: 306px;
}

.block-title__line {
  width: 100%;
  border-bottom: 1px solid;
  border-color: #f5f5f5;
}

.subject-content {
  display: flex;
}

.atlas-info {
  display: flex;
  flex-direction: column;
  padding: 0 !important;
}

.top-prof {
  display: flex;
  flex-direction: column;

  border: 1px solid #8697ef;
  border-radius: 4px;
  box-shadow: 0px 0px 0px 5px #f1f3f8;

  margin-bottom: 25px;
}

.top-prof__header {
  font-family: "Russia";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;

  padding: 19px 26px;

  color: #22262d;

  border-bottom: 5px solid #8697ef;
}

.top-prof__subject {
  font-family: "Russia";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;

  padding-bottom: 30px;

  color: #22262d;
}

.top-prof__title {
  text-transform: uppercase;
  font-weight: 700;
}

.top-prof__item {
  font-family: "Russia";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  text-transform: lowercase;

  display: flex;
  margin-bottom: 3px;
  padding-left: 52px;
  padding-right: 64px;

  align-items: center;

  color: #000000;
}

.top-prof__item--active {
  background: #f1f3f8;
  padding-top: 11px;
  padding-left: 34px;
}

.top-prof__item svg {
  padding-bottom: 7px;
}

.svg-wrapper {
  width: 30px;
  margin-left: 13px;
  margin-right: 24px;
}

.top-prof__item span {
  border-bottom: 1px solid #f5f5f5;
  width: 100%;
  padding-bottom: 7px;
}

.subject-content .subject-content__map {
  width: 100%;
  padding-left: 7%;
}

.top-prof__list {
  list-style: none;
  margin-top: 6px;
  padding: 0;
}

.button-listing {
  display: flex;

  background: #f1f3f8;
  border-radius: 4px;

  padding: 25px;
  margin-bottom: 14px;

  border: none;

  font-family: "Russia";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  display: flex;
  align-items: center;
  text-align: start;

  color: #22262d;
}

.button-listing p {
  max-width: 290px;
  margin: 0;
}

.button-listing svg {
  margin-right: 25px;
}

.atlas-info-list {
  display: flex;
  gap: 9px;
  list-style: none;
  padding: 0;
}

.atlas-info-item {
  padding: 12px;
  border: 1px solid #f1f3f8;
  border-radius: 4px;

  width: 100%;

  display: flex;
  flex-direction: column;

  font-family: "Russia";
  font-style: normal;
  font-weight: 400;
  font-size: 9px;
  line-height: 14px;

  color: #000000;
}

.atlas-info-item img {
  align-self: center;
}

.atlas-poo {
  display: flex;
  flex-direction: column;

  margin-top: 28px;
}

.atlas-poo__title {
  display: flex;
  gap: 9px;
  margin-bottom: 22px;

  align-items: center;
}

.atlas-poo__title span {
  background: #8697ef;
  border-radius: 2px;

  padding: 2px 20px;
  min-width: 140px;

  font-family: "Russia";
  font-style: normal;
  font-size: 16px;
  line-height: 18px;
  display: flex;
  align-items: center;
  text-transform: uppercase;

  color: #ffffff;
}

.atlas-poo__list {
  list-style: none;
  display: flex;
  flex-direction: column;

  font-family: "Russia";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;

  color: #000000;
  padding: 0;
}

@media (max-width: 999px) {
  .subject-content {
    flex-direction: column;
  }
}

@media (max-width: 500px) {
  .atlas-filter {
    flex-direction: column;
  }
}
