.content__body--atlas {
  padding-top: 30px;
  padding-bottom: 220px;
}

.button.btn-nav {
  border: 1px solid #224390;
  border-radius: 4px;
  background-color: white;
  color: #224390;
}

.svg-nav {
  margin-right: 10px;
}

.pro-atlas--heading {
  background-position: center;
  height: 167px;
  background-image: url(../img/atlas-header.png);

  position: relative;
  max-width: 1300px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 20px;
  padding-left: 20px;
}

.title.atlas__title {
  color: #224390;
}

.title.atlas__subtitle {
  color: #224390;
  text-transform: none;
  margin: 60px 0 110px;
}

.filter.atlas__filter {
  margin: 0;
  max-width: 938px;
  padding: 0;
  background: none;
}

.atlas__filter-field {
  margin-right: 20px;
  padding: 10px;
  background: #f5f5f5;
  border-radius: 2px;
}

.button.atlas__search-button {
  background: #8697ef;
  border-color: #8697ef;
  border-radius: 4px;
  padding: 0 64px;
}

.atlas-filter {
  display: flex;
  padding: 0;
  list-style: none;
}

.atlas-filter__item {
  padding: 6px 21px 0;
  border-right: 1px solid #f5f5f5;
}

.atlas-filter__link {
  text-decoration: none;
  font-family: "Russia";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;

  color: #224390;
}

