.title.atlas__vacancy-title {
  color: #224390;
  text-transform: none;
  margin: 0;
  min-width: 506px;
  font-weight: 700;
}

.vacancy__subject {
  margin-top: 26px;

  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;

  color: #224390;
}

.block-title__vacancy {
  margin-top: 0;
  margin-bottom: 32px;
}

.vacancy-info {
  display: flex;
}

.vacancy-icon {
  display: flex;
  flex-direction: column;
}

.vacancy-info__wrapper {
  display: flex;
  flex-direction: column;

  padding-right: 130px;
  max-width: 767px;

  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
}

.vacancy-info__wrapper p {
  padding-left: 20px;
}

.vacancy-date {
  font-family: "Russia";
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 16px;
  color: #3fbac2;

  padding-top: 12px;
}

.vacancy-info__highlighting {
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;

  color: #3fbac2;
}

.vacancy-info__title {
  width: max-content;
  padding: 5px 20px;
  margin-left: 8px;
  margin-bottom: 0;

  background: #8697ef;
  font-family: "Russia";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  display: flex;
  align-items: center;
  text-transform: uppercase;

  color: #fff;
}

.vacancy-info__list {
  list-style: none;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 6px;
  text-transform: lowercase;

  color: #000000;

  padding-top: 10px;
  padding-left: 22px;
}

.vacancy-info__link {
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;

  color: #224390;
  text-transform: none;
  text-decoration: none;
}

.block-title__vacancy--info {
  margin-top: 32px;
  margin-bottom: 12px;
}

.vacancy-info__title--skills {
  min-width: 264px;
}

.vacancy-info__title--upskills {
  min-width: 296px;
}

.vacancy-info__title--poo {
  min-width: 186px;
}

.vacancy-info__title--salary {
  min-width: 334px;
}

.vacancy-info__title--employer {
  min-width: 154px;
}

.vacancy-info__salary {
  display: flex;
  align-items: center;
  padding: 17px 0 0 20px;
  gap: 15px;

  font-family: "Russia";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;

  color: #000000;
}

.vacancy-info__employers {
  display: flex;
  flex-direction: column;
  list-style: none;

  padding-left: 22px;
  padding-top: 9px;

  font-family: "Russia";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  text-decoration-line: underline;

  color: #000000;
}

.vacancy-info__employers--item {
  display: flex;
  gap: 17px;
}

@media (max-width: 999px) {
  .vacancy-info__wrapper {
    padding-right: 0;
  }

  .vacancy-icon {
    display: none;
  }
}
